@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root {
  --button-panel-height: 3rem;
  --theme-color: #BF202F;
  --theme-color-dark: #8F181A;
}

body {
  margin: 0;
  background-color: #F9F8F7 !important;
  color: #3D3D3D !important;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600 !important;
}

main {
  max-width: 1284px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1rem;
}
.navbar {
    background-color: #FFFFFF !important;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
    margin-bottom: 1rem;
}

.nav-link.nav-link-active,
.nav-link.nav-link-active:hover,
.nav-link.nav-link-active:focus {
    color: var(--theme-color) !important;
    text-decoration: underline;
}

.greeting {
    color: #000 !important;
}
.add-inventory-panel {
    padding: 1.5rem 2rem;
    border: 3px dashed var(--theme-color);
    border-radius: 25px;
}

.add-inventory-btn {
    color: inherit !important;
    text-decoration: none !important;
}

.camera-view {
    padding-bottom: var(--button-panel-height);
}

.camera-area {
    display: flex;
    margin: 0;
    border: 3px dashed var(--theme-color);
    border-radius: 25px;
    height: 57vh;
    overflow: hidden;
    position: relative;
}

.camera-area video {
    top: 0px;
    left: 0px;
    display: block;
    position: absolute;
    overflow: hidden;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
@media screen and (min-width: 1284px) {
	.sidebar-control {
		display: none !important;
	}
}

.inventory-page {
}

.inventory-page-header {
	margin-bottom: 0.5rem;
	padding-left: 1.5rem;
}

.inventory-page-header h2 {
	color: var(--theme-color);
	font-weight: bolder;
	text-align: center;
	display: inline-block;
}

.inventory-page-body {
	display: flex;
	justify-content: flex-start;
	flex-flow: row nowrap;
	overflow: hidden;
}

.category-sidebar {
	min-width: 200px;
	height: 90vh;
	box-shadow: 2px 0 3px 0 rgba(0, 0, 0, 0.25);
	background-color: #FFFFFF;
	border-left: 1px solid #DDD;
	margin-right: 2.5rem;
	position: relative;
	transition: all 0.5s ease;
}

.category-sidebar.hidden {
	margin-left: -200px;
	box-shadow: none;
}

.category-sidebar.hidden li.active {
	transform: none;
}

.category-sidebar.hidden .arrow-right {
	display: none;
}

.category-sidebar ul {
	width: 100%;
    display: flex;
    flex-direction: column;
   
	line-height: 0;
    list-style: none;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
}

.category-sidebar ul li {
    width: 100%;
    margin-top: 1px;
	cursor: pointer;
	padding: 20px 1rem;
	transition: all .3s ease-out;
	position: relative;
}

.category-sidebar ul li:hover,
.category-sidebar ul li.active {
	transform: scale(1.05);
	border-radius: 0.1cm;
}

.category-sidebar ul li.active .arrow-right {
	height: 45px;
	width: 45px;
	position: absolute;
	right: -16px;
	bottom: -3px;
	transform: rotate(45deg);
}

.sidebar-control {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	width: 50px;
	height: 50px;
	bottom: 0.5em;
	left: 0.5em;
	background-color: var(--theme-color);
	border-radius: 50%;
	opacity: 0.75;
	cursor: pointer;
	z-index: 9999;
}

.sidebar-control.hidden {
	transform: rotate(180deg);
}

.sidebar-control img {
	width: 25px;
	height: 25px;
}

.inventory-list {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
	align-content: flex-start;
}

.inventory-list .card {
	display: inline-flex;
	padding: 1rem;
	width: 200px;
	height: 250px;
	margin-right: 2rem;
	margin-bottom: 2rem;
	cursor: pointer;
	position: relative;
	box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
}

.card .cardImage {
	width: 100%;
	height: 75%;
	display: flex;
	align-items: center;
	overflow: hidden;
	margin-bottom: 3px;
}

.card .cardImage img {
	width: 100%;
	border-radius: 7%;
}

.card .card-text {
	margin: 0;
	width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

.card .card-text.light {
	font-size: 12px;
	font-weight: normal;
}

.card .cardQty {
	display: inline-block;
	width: auto;
	position: absolute;
	bottom: 0.35rem;
	right: 0.75rem;
}

.qty {
	color: var(--theme-color);
}


.map-wrapper {
    position: relative;
    width: 100%;
    height: 50vh;
    border-radius: 20px;
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.25);
}

.location-image {
    display: block;
    border-radius: 5px;
    width: 18rem;
    margin-bottom: 0.5rem;
}

.location-name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 0;
}

.location-description {
    font-style: italic;
}

.location-address {
    font-size: 14px;
}
.receipt-view {
    padding-bottom: var(--button-panel-height);
}

.receipt-panel {
    margin-bottom: 1.5rem;
}

.receipt-panel h2 {
    color: var(--theme-color);
    font-size: 20px;
    font-weight: bold;
}

.location-item {
    min-height: 7.5rem;
    background-color: #FFFFFF;
    border-radius: 12px;
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    overflow: hidden;

    display: grid;
    grid-template-columns: minmax(-webkit-min-content, 10%) minmax(-webkit-min-content, 90%);
    grid-template-columns: minmax(min-content, 10%) minmax(min-content, 90%);
}

.location-logo,
.receipt-item-image {
    display: flex;
    align-items: center;
    height: 100%;
}

.location-logo img,
.receipt-item-image img {
    width: 100%;
}

.location-body {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding: 0.5rem 1rem;
}

.location-name,
.receipt-item-name {
    color: var(--theme-color);
    font-size: 20px;
    font-weight: 500;
}

.receipt-item {
    background-color: #FFFFFF;
    border-radius: 12px;
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);

    display: grid;
    grid-template-columns: minmax(-webkit-min-content, 10%) minmax(-webkit-min-content, 95%) 5%;
    grid-template-columns: minmax(min-content, 10%) minmax(min-content, 95%) 5%;
    margin-bottom: 2rem;
}

.receipt-item-body {
    padding: 0.5rem 1rem;
}

.receipt-item-brand {
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
}

.receipt-item-brand img {
   max-width: 25px;
   margin-right: 5px;
}

.receipt-item-categories {
    display: flex;
    align-items: center;
}

.receipt-item-category-item {
    padding: 0.375rem 0.5rem;
    margin-left: 5px;
    border-radius: 5px;
}

.receipt-item-category-item.vegetable {
    background-color: #77B255;
    color: #FFFFFF;
}

.receipt-item-remove-btn {
    font-size: 4rem !important;
    overflow: hidden;
}

.add-receipt-item {
    padding: 1.5rem 2rem;
    border: 3px dashed var(--theme-color);
    border-radius: 25px;
}
.btn-default {
    color: #FFFFFF;
    background-color: var(--theme-color);
    border-color: var(--theme-color);
}
  
.btn-default:hover {
    color: #FFFFFF;
    background-color: var(--theme-color-dark);
    border-color: var(--theme-color-dark);
}

.btn-default:focus, .btn-secondary.focus {
    color: #FFFFFF;
    background-color: var(--theme-color-dark);
    border-color: var(--theme-color-dark);
    box-shadow:  0 0 0 0.2rem rgba(143, 24, 26, 0.25);
}

.btn-default:not(:disabled):not(.disabled):active,
.btn-default:not(:disabled):not(.disabled).active,
.show > .btn-default.dropdown-toggle {
    color: #FFFFFF;
    background-color: var(--theme-color-dark);
    border-color: var(--theme-color-dark);
}

@media screen and (max-width: 575px) {
    .right-bottom-border-sm {
        border-right: none !important;
        border-bottom: 1px solid var(--theme-color);
        padding-bottom: 0.5em;
        margin-bottom: 1rem;;
    }
}

@media screen and (max-width: 767px) {
    .paragraph-1 {
        font-size: 18px !important;
    }

    .right-bottom-border-md {
        border-right: none !important;
        border-bottom: 1px solid var(--theme-color);
        padding-bottom: 0.5em;
        margin-bottom: 1rem;;
    }
}

.right-bottom-border-sm,
.right-bottom-border-md {
    border-right: 1px solid var(--theme-color);
}

.paragraph-1 {
    text-align: center;
    color: var(--theme-color);
    font-size: 24px;
    font-weight: bold;
}

.button-panel {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: var(--button-panel-height);
}

.button-panel button {
    height: 100%;
    font-size: 20px;
    font-weight: bolder;
}

.back-btn {
    border: 2px solid #C9C9C9;
    background-color: #EAEAEA;
    color: #898989;
}

.confirm-btn {
    border: 2px solid var(--theme-color-dark);
    background-color: var(--theme-color);
    color: #FFF;
}
